import React from "react";
import { BrowserRouter, Switch, Route} from "react-router-dom";
import LandingPage from '../Pages/LandingPage'
import Blog from '../Pages/Blog'


function App() {
  return (
   <BrowserRouter>
    <Switch>
      <Route  exact path="https://elimasyrubi.github.io/landinpage" component={LandingPage} />
      <Route  exact path="https://elimasyrubi.github.io/landinpage/blog" component={Blog} />
    </Switch>
   </BrowserRouter>

  );
}

export default App;
