import React from "react";
import './Styles/Navbar.scss'
import { Link } from 'react-router-dom'

function Navbar(props) {
  return (
      <nav className=" nav">
        <div className="container g-area row ">
          <Link to="https://elimasyrubi.github.io/landinpage/" className=" nav_logo glogo">My Logo</Link>
          <p className="nav_text gtexto">Tu negocio ha llegado a la era digital</p>
          <Link to={props.url} className=" btn btn-outline-light nav_blog  gblog">{props.name}</Link>
        </div>
      </nav>
  );
}

export default Navbar;
 