import React from "react";
import './styles/Blog.scss'
import Navbar from '../Component/Navbar'
import Footer from '../Component/Footer'
import Boton from '../Component/Boton'

function Blog() {
  return (
    <div>
      <Navbar name='Inicio' url='https://elimasyrubi.github.io/landinpage/'/>
        <div className=" container-blog mt-4 mb-4">
        <h1> Te presento las 10 principales razones por las cuales es importante estar en Internet</h1>
        <p><span>1. Presencia local, nacional y mundial: porque tus productos y servicios serán visibles para tus potenciales clientes desde cualquier parte del mundo. La ubicación geográfica de tu negocio ya no tendrá importancia.</span> </p>
        <p> <span>2. Ampliación de horarios: </span>porque podrás tener tu negocio abierto al público 24 horas al día, 365 días al año sin gastos de alquiler, personal, luz.  </p>
        <p> <span>3. Ahorro de dinero y tiempo: </span> porque podrás brindar información detallada de productos, catálogos, etc. por internet ahorrándote dinero en documentación impresa. Además tus clientes pueden conocer tus productos o servicios antes de contactarte por teléfono.</p>
        <p> <span>4. Conseguir nuevos clientes: </span> porque podrás acceder a nuevos clientes estén donde estén y fidelizar los que ya tienes. </p>
        <p> <span> 5. Publicidad más eficiente: </span> porque la publicidad por Internet es más barata y contante. Además selecciona mejor al público objetivo. </p>
        <p> <span>6. Mejor imagen y prestigio: </span>   porque tener presencia en Internet refuerza y actualiza la imagen corporativa de tu empresa. Permite dar a conocer a tus clientes potenciales que tu empresa está siempre a la vanguardia en tecnología e información.</p>
        <p> <span>  7. Crecimiento de las ventas: </span>  porque tener una tienda online es la mejor manera de ofrecer nuevos productos y servicios de forma rápida y eficiente. </p>
        <p> <span>8. Mejora tu comunicación:  </span>  porque permite una comunicación más rápida y directa con clientes y proveedores.</p>
        <p> <span>9. Adelantar a la competencia</span> porque en internet es donde hay que estar y no se puede ser el último.</p>
        <p> <span>10. Tu tarjeta de presentación en internet: </span> porque una página web es la tarjeta de presentación de todo el que se dedique a una actividad profesional o comercial. Además podrá ser fácilmente referenciada a otras personas. </p>
        <p>Existen muchos otros beneficios y ventajas adicionales que varían dependiendo de la naturaleza de tu negocio.</p>  
        </div>

      <Boton/>
      <Footer/>
      
    </div>
  );
}


export default Blog;
